export default function Icon({ w, h, color, rotate, loc }) {
  const style = () => {
    if (loc === "left") {
      return {
        position: "absolute",
        top: "-.3rem",
        left: "-.3rem"
      };
    }
  };

  return (
    <svg width={w} height={h} viewBox="0 0 100 100" style={style()}>
      <path
        fill="none"
        transform={rotate}
        style={{
          WebkitTransformOrigin: "50%50%",
          MsTransformOrigin: "50%50%",
          transformOrigin: "50%50%"
        }}
        stroke={color}
        strokeWidth="3"
        d="M10 10L90 10 90 90"
      ></path>
    </svg>
  );
}
